@import url('https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap');

body {
  font-family: 'Patrick Hand', cursive;
}

#svg {
  width: 50%;
}

@media screen and (max-width: 576px) {
  #svg {
    width: 80%;
  }
}

.cls-1,
.cls-10 {
  fill: #f2f2f2;
}

.cls-2 {
  fill: #92a89a;
}

.cls-10,
.cls-11,
.cls-14,
.cls-2,
.cls-3,
.cls-6,
.cls-7 {
  stroke: #000;
}

.cls-10,
.cls-11,
.cls-14,
.cls-17,
.cls-2,
.cls-20,
.cls-22,
.cls-3,
.cls-5,
.cls-6,
.cls-7 {
  stroke-miterlimit: 10;
}

.cls-13,
.cls-3 {
  fill: #e9bda0;
}

.cls-3 {
  stroke-width: 0.5px;
}

.cls-4 {
  opacity: 0.07;
}

.cls-20,
.cls-22,
.cls-5 {
  stroke: #4d4d4d;
}

.cls-10,
.cls-11,
.cls-14,
.cls-17,
.cls-20,
.cls-22,
.cls-5,
.cls-6,
.cls-7 {
  stroke-width: 0.25px;
}

.cls-5 {
  fill: url(#linear-gradient);
}

.cls-21,
.cls-22,
.cls-7 {
  fill: #333;
}

.cls-8 {
  fill: #f1c3af;
  opacity: 0.74;
}

.cls-9 {
  clip-path: url(#clip-path);
}

.cls-11 {
  fill: #271a1a;
}

.cls-12 {
  clip-path: url(#clip-path-2);
}

.cls-14,
.cls-20 {
  fill: none;
}

.cls-15 {
  fill: #42210b;
}

.cls-16 {
  opacity: 0.37;
}

.cls-17 {
  fill: #1a1a1a;
  stroke: #e6e6e6;
}

.cls-18 {
  fill: #6f2228;
  opacity: 0.43;
}

.cls-19 {
  fill: #d58f99;
}

@keyframes reveal {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 100;
  }
}

#Nose, #Eye, #Lips {
  animation: reveal 3s linear;
  animation-delay: 1s;
  opacity: 0;
  animation-fill-mode: forwards;
}


#rightEye {
  animation: moveRight 6s infinite;
  -webkit-animation: moveRight 6s infinite;
  animation-delay: 5s;
}

#leftEye {
  animation: moveLeft 6s infinite;
  -webkit-animation: moveLeft 6s infinite;
  animation-delay: 5s;
}

@keyframes moveRight {
  0% {
    transform: translateX(0px);
    -webkit-transform: translateX(0px);
    -moz-transform: translateX(0px);
    -ms-transform: translateX(0px);
    -o-transform: translateX(0px);
  }

  50% {
    transform: translateX(10px);
    -webkit-transform: translateX(10px);
    -moz-transform: translateX(10px);
    -ms-transform: translateX(10px);
    -o-transform: translateX(10px);
  }

  0% {
    transform: translateX(0px);
    -webkit-transform: translateX(0px);
    -moz-transform: translateX(0px);
    -ms-transform: translateX(0px);
    -o-transform: translateX(0px);
  }
}

@keyframes moveLeft {
  0% {
    transform: translateX(0px);
    -webkit-transform: translateX(0px);
    -moz-transform: translateX(0px);
    -ms-transform: translateX(0px);
    -o-transform: translateX(0px);
  }

  50% {
    transform: translateX(11px);
    -webkit-transform: translateX(11px);
    -moz-transform: translateX(11px);
    -ms-transform: translateX(11px);
    -o-transform: translateX(11px);
  }

  0% {
    transform: translateX(0px);
    -webkit-transform: translateX(0px);
    -moz-transform: translateX(0px);
    -ms-transform: translateX(0px);
    -o-transform: translateX(0px);
  }
}

#rightEyelid {
  animation: blinkRightEye 4.25s infinite;
  -webkit-animation: blinkRightEye 4.25s infinite;
}

#leftEyelid {
  animation: blinkRightEye 4.25s infinite;
  -webkit-animation: blinkRightEye 4.25s infinite;
}

@keyframes blinkRightEye {
  0% {
    transform: translate(-466.7px, -501.38px);
    -webkit-transform: translate(-466.7px, -501.38px);
    -moz-transform: translate(-466.7px, -501.38px);
    -ms-transform: translate(-466.7px, -501.38px);
    -o-transform: translate(-466.7px, -501.38px);
  }

  50% {
    transform: translate(-466.7px, -469.38px);
    -webkit-transform: translate(-466.7px, -469.38px);
    -moz-transform: translate(-466.7px, -469.38px);
    -ms-transform: translate(-466.7px, -469.38px);
    -o-transform: translate(-466.7px, -469.38px);
  }

  100% {
    transform: translate(-466.7px, -501.38px);
    -webkit-transform: translate(-466.7px, -501.38px);
    -moz-transform: translate(-466.7px, -501.38px);
    -ms-transform: translate(-466.7px, -501.38px);
    -o-transform: translate(-466.7px, -501.38px);
  }
}